import { Fragment, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { updateUserData } from '../../../../redux/userReducer';

const EditCustomerModal = ({isModal, setIsModal, customerData}) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [tel, setTel] = useState('');
  const [email, setEmail] = useState('');
  const [address, setAddress] = useState('');
  const [postcode, setPostCode] = useState('');
  const [notes, setNotes] = useState('');

  const handleConfirm = () => {  
    customerData.businessname = username;
    customerData.telephone = tel;
    customerData.email = email;
    customerData.address = address;
    customerData.postcode = postcode;
    customerData.notes = notes;
    dispatch(updateUserData(customerData._id, customerData));

    setUsername(''); setPassword(''); setTel(''); setEmail(''); setAddress(''); setPostCode(''); setNotes('');
    setIsModal(false);
  }

  useEffect(() => {
    if (isModal && customerData) {
      setUsername(customerData ? customerData.businessname : '');
      setPassword(customerData ? customerData.password : '');
      setTel(customerData ? customerData.telephone : '');
      setEmail(customerData ? customerData.email : '');
      setAddress(customerData ? customerData.address : '');
      setPostCode(customerData ? customerData.postcode : '');
      setNotes(customerData ? customerData.notes : '');
    }
  }, [customerData._id, isModal])

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full">
              <div className="relative bg-[#404040] w-[40vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className='mb-2'>
                  <h1 className='text-xl text-left text-[#D9D9D9] select-none'>Add/Edit Customer</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[20rem] pb-[0.5rem]">
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Username:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="text"
                        value={username}
                        onChange={e => setUsername(e.target.value)} 
                    />
                  </div>
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Password:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="password"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                    />
                  </div>
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Tel:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="text"
                        value={tel}
                        onChange={e => setTel(e.target.value)}
                    />
                  </div>
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Email:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="text"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                    />
                  </div>
                  <div>
                    <div className="flex flex-row items-center py-1">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Address:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={address}
                          onChange={e => setAddress(e.target.value)}
                      />
                    </div>
                  </div>
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Post Code:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="text"
                        value={postcode}
                        onChange={e => setPostCode(e.target.value)}
                    />
                  </div>
                  <div className="flex flex-row items-center py-1">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                      Notes:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                        type="text"
                        value={notes}
                        onChange={e => setNotes(e.target.value)}
                    />
                  </div>
                </div>                
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Confirm</button>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default EditCustomerModal;