import { Fragment, useState } from 'react';
// import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch } from 'react-redux';
import { updateUserproxyData } from '../../../../redux/userReducer';
import { openSnackBar } from '../../../../redux/snackBarReducer';
import { userService } from '../../../../services/users.service';

const AddUserproxyModal = ({isModal, setIsModal, seluser}) => {
  // const navigate = useNavigate();
  const dispatch = useDispatch();

  const [username, setUsername] = useState('');
  const [userpin, setUserpin] = useState('');
  const [usernameIssue, setUsernameIssue] = useState('');
  const [pinIssue, setPinIssue] = useState('');

  const handleConfirm = async () => { 
    const checking = checkValidation();
    if (!checking.status) {
      dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
      return;
    }

    const res = await userService.checkUserByProxyName(username);
    if (res.result) {
      dispatch(openSnackBar({ message: `User with username: ${username} already exists. Please retype your username.`, status: 'error' }));
      return;        
    }

    dispatch(updateUserproxyData(0, {username, userpin, email: seluser.email, role: seluser.role}));

    setUsername(''); setUserpin('');
    setIsModal(false);
  }

  const checkValidation = () => {
    const ret = {message: "", status: false};
    if (username.length === 0) {ret.message = "Please input username"; ret.status = false; setUsernameIssue(true);}
    else if (userpin.length === 0) {ret.message = "Please input pin"; ret.status = false; setPinIssue(true);}
    else { ret.message = ""; ret.status = true;}
    return ret;
  }

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full">
              <div className="relative bg-[#404040] w-[40vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className='mb-2'>
                  <h1 className='text-xl text-left text-[#D9D9D9] select-none'>Add User</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[20rem] pb-[0.5rem]">
                  <div className="flex flex-row items-center">
                    <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[60%]">
                      <div className="flex flex-row items-center">
                        <label className="block uppercase tracking-wide text-gray-100 mb-1">
                          Name:
                        </label>
                        <input 
                            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                            type="text"
                            value={username}
                            onChange={e => {setUsername(e.target.value); setUsernameIssue(false)}} 
                        />
                      </div>
                      <p className={`${usernameIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Name must be >2 characters'}</p>
                    </div>
                    <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[40%]">
                      <div className="flex flex-row items-center">
                        <label className="block uppercase tracking-wide text-gray-100 mb-1">
                          PIN:
                        </label>
                        <input 
                            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                            type="text"
                            value={userpin}
                            onChange={e => {setUserpin(e.target.value); setPinIssue(false)}} 
                        />
                      </div>
                      <p className={`${pinIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>PIN should be 4 digits</p>
                    </div>
                  </div>
                </div>                
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Confirm</button>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default AddUserproxyModal;