import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faPlus, faTrash, faEdit } from '@fortawesome/free-solid-svg-icons';
import { SUPPORT_COINS } from "../../../config/constants";
import Switch from "react-switch";
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {
  selectCoinAllowBTC,
  selectCoinAllowETH,
  selectCoinAllowBNB,
  selectCoinAllowXRP,
  selectCoinAllowUSDT,
  selectCoinAllowCPOS,
  setCoinAllowBTC,
  setCoinAllowETH,
  setCoinAllowBNB,
  setCoinAllowXRP,
  setCoinAllowUSDT,
  setCoinAllowCPOS,
  selectAllowDiscount,
  setAllowDiscount
} from '../../../redux/settingReducer';
import { selectAllUserproxys, selectAllCustomers, getAllUserproxyData, getAllCustomerData, selectUser } from '../../../redux/userReducer';
import { useEffect } from 'react';
import AddUserproxyModal from './modals/add.userproxy.modal';
import AddCustomerModal from './modals/add.customer.modal';
import DeleteUserproxyModal from './modals/delete.userproxy.modal';
import DeleteUserModal from './modals/delete.user.modal';
import EditUserproxyModal from './modals/edit.userproxy.modal';
import EditCustomerModal from './modals/edit.customer.modal';
import ContactSupportModal from './modals/contact.support.modal';
import { userService } from '../../../services/users.service';
import { openSnackBar } from '../../../redux/snackBarReducer';

const Settings = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const coinBTC = useSelector(selectCoinAllowBTC);
  const coinETH = useSelector(selectCoinAllowETH);
  const coinBNB = useSelector(selectCoinAllowBNB);
  const coinXRP = useSelector(selectCoinAllowXRP);
  const coinUSDT = useSelector(selectCoinAllowUSDT);
  const coinCPOS = useSelector(selectCoinAllowCPOS);
  const userproxys = useSelector(selectAllUserproxys);
  const customers = useSelector(selectAllCustomers);
  const discount = useSelector(selectAllowDiscount);
  const [userproxyEditModal, setUserproxyEditModal] = useState(false);
  const [customerEditModal, setCustomerEditModal] = useState(false);
  const [userproxyModal, setUserproxyModal] = useState(false);
  const [customerModal, setCustomerModal] = useState(false);
  const [userproxyDeleteModal, setUserproxyDeleteModal] = useState(false);
  const [customerDeleteModal, setCustomerDeleteModal] = useState(false);
  const [contactModal, setContactModal] = useState(false);
  const seluser = useSelector(selectUser);
  const [userproxyID, setUserproxyID] = useState("");
  const [customerID, setCustomerID] = useState("");
  const [userproxyData, setUserproxyData] = useState([]);
  const [customerData, setCustomerData] = useState([]);
  
  const getAllowCoin = (item) => {
    let ret = coinBTC;
    switch (item) {
      case SUPPORT_COINS[0]:
        ret = coinBTC;
        break;
      case SUPPORT_COINS[1]:
        ret = coinETH;
        break;
      case SUPPORT_COINS[2]:
        ret = coinBNB;
        break;
      case SUPPORT_COINS[3]:
        ret = coinXRP;
        break;
      case SUPPORT_COINS[4]:
        ret = coinUSDT;
        break;
      case SUPPORT_COINS[5]:
        ret = coinCPOS;
        break;
    }

    return ret;
  }

  const handleAllowDiscount = async (value) => {
    dispatch(setAllowDiscount(!value));
    await userService.updateAllowDiscount(seluser._id, {discount: !value});
  }

  const handleAllowCoin = async (item) => {
    switch (item) {
      case SUPPORT_COINS[0]:
        const btc = !coinBTC;
        dispatch(setCoinAllowBTC(btc));
        await userService.updateCoinValid(seluser._id, {btc: btc, eth: coinETH, bnb: coinBNB, xrp: coinXRP, usdt: coinUSDT, cpos: coinCPOS});
        break;
      case SUPPORT_COINS[1]:
        const eth = !coinETH;
        dispatch(setCoinAllowETH(eth));
        await userService.updateCoinValid(seluser._id, {btc: coinBTC, eth: eth, bnb: coinBNB, xrp: coinXRP, usdt: coinUSDT, cpos: coinCPOS});
        break;
      case SUPPORT_COINS[2]:
        const bnb = !coinBNB;
        if (bnb) {
          dispatch(openSnackBar({ message: `You cannot active this now. Comming soon.`, status: 'error' }));
          return;
        }
        dispatch(setCoinAllowBNB(bnb));
        await userService.updateCoinValid(seluser._id, {btc: coinBTC, eth: coinETH, bnb: bnb, xrp: coinXRP, usdt: coinUSDT, cpos: coinCPOS});
        break;
      case SUPPORT_COINS[3]:
        const xrp = !coinXRP;
        if (xrp) {
          dispatch(openSnackBar({ message: `You cannot active this now. Comming soon.`, status: 'error' }));
          return;
        }
        dispatch(setCoinAllowXRP(xrp));
        await userService.updateCoinValid(seluser._id, {btc: coinBTC, eth: coinETH, bnb: coinBNB, xrp: xrp, usdt: coinUSDT, cpos: coinCPOS});
        break;
      case SUPPORT_COINS[4]:
        const usdt = !coinUSDT;
        dispatch(setCoinAllowUSDT(usdt));
        await userService.updateCoinValid(seluser._id, {btc: coinBTC, eth: coinETH, bnb: coinBNB, xrp: coinXRP, usdt: usdt, cpos: coinCPOS});
        break;
      case SUPPORT_COINS[5]:
        const cpos = !coinCPOS;
        if (cpos) {
          dispatch(openSnackBar({ message: `You cannot active this now. Comming soon.`, status: 'error' }));
          return;
        }
        dispatch(setCoinAllowCPOS(cpos));
        await userService.updateCoinValid(seluser._id, {btc: coinBTC, eth: coinETH, bnb: coinBNB, xrp: coinXRP, usdt: coinUSDT, cpos: cpos});
        break;
    }
  }

  const handleDeleteUserproxyModal = (userproxyid) => {
    setUserproxyID(userproxyid);
    setUserproxyDeleteModal(true);
  }

  const handleDeleteCustomerModal = (customerid) => {
    setCustomerID(customerid);
    setCustomerDeleteModal(true);
  }

  const handleEditUserModal = async (userproxyid) => {
    const u = await userService.getUserproxyById(userproxyid);
    setUserproxyData(u.data);  
    setUserproxyEditModal(true); 
  }

  const handleEditCustomerModal = async (customerid) => {
    const u = await userService.getUserById(customerid);
    setCustomerData(u.data);
    setCustomerEditModal(true);
  }

  useEffect( () => {
    if (seluser && seluser._id) {
      dispatch(getAllUserproxyData(seluser.email, seluser.role));
      dispatch(getAllCustomerData());
    }
  }, [seluser._id])
  
  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)] relative">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Settings</p>
              </div>
              <div className='w-full mt-[1rem] text-gray-100 text-md'>
                <div className='px-[2rem] text-xl mb-2'>Toggle Accepted Cryptocurrencies</div>
                <div className='bg-[#333333] w-full'>
                  {
                    SUPPORT_COINS.map((item, index) => {
                      if (index === 2 || index === 3 || index === 5) return null;
                      return (
                        <div key={index} className='w-full flex items-center text-gray-100 px-[1rem] leading-[1.8rem] border-b border-[#282828]'>
                          <p className='w-[60%]'>{item}</p>
                          <p className='w-[20%] text-right'>{getAllowCoin(item) ? 'Active' : 'Inactive'}</p>
                          <div className='w-[20%] text-right flex items-center justify-end'>
                            <Switch onChange={()=>{handleAllowCoin(item)}} checked={getAllowCoin(item)?getAllowCoin(item):false} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                          </div>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
              <div className='w-full mt-[1rem] text-gray-100 text-md'>
                <div className='px-[2rem] text-xl mb-2'>Discount Append</div>
                <div className='bg-[#333333] w-full'>
                  <div className='w-full flex items-center text-gray-100 px-[1rem] leading-[1.8rem] border-b border-[#282828]'>
                    <p className='w-[60%]'>Discount</p>
                    <p className='w-[20%] text-right'>{discount ? 'Active' : 'Inactive'}</p>
                    <div className='w-[20%] text-right flex items-center justify-end'>
                      <Switch onChange={()=>{handleAllowDiscount(discount)}} checked={discount?true:false} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                  </div>
                </div>
              </div>
              {/* <div className='w-full mt-[1rem] text-gray-100 text-md'>
                <div className='px-[2rem] text-xl mb-2 flex items-center'>
                  <p className='mr-4'>Manage Users</p>
                  <button 
                      className={`w-[1.6rem] h-[1.6rem] text-sm rounded-[2px] leading-[1.6rem] bg-[#D9D9D9] text-gray-700`}
                      onClick={() => setUserproxyModal(true)}
                  ><FontAwesomeIcon icon={faPlus}/></button>
                </div>
                <div className='bg-[#333333] w-full h-[15vh] overflow-y-auto'>
                  {
                    userproxys.map((userproxy, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 px-[1rem] leading-[1.8rem] border-b border-[#282828]'>
                          <p className='w-[60%]'>{userproxy.username}</p>
                          <p className='w-[20%] text-right'>Staff</p>
                          <p className='w-[20%] text-right flex gap-6 justify-end'>
                            <button 
                                className={`text-md text-gray-100`}
                                onClick={() => handleDeleteUserproxyModal(userproxy._id)}
                            ><FontAwesomeIcon icon={faTrash}/></button>
                            <button 
                                className={`text-md text-gray-100`} 
                                onClick={() => handleEditUserModal(userproxy._id)}             
                            ><FontAwesomeIcon icon={faEdit}/></button>
                          </p>
                        </div>
                      )
                    })
                  }
                </div>
              </div> */}
              <div className='w-full mt-[1rem] text-gray-100 text-md'>
                <div className='px-[2rem] text-xl mb-2 flex items-center'>
                  <p className='mr-4'>Manage Customers</p>
                  <button 
                      className={`w-[1.6rem] h-[1.6rem] text-sm rounded-[2px] leading-[1.6rem] bg-[#D9D9D9] text-gray-700`}
                      onClick={() => setCustomerModal(true)}
                  ><FontAwesomeIcon icon={faPlus}/></button>
                </div>
                <div className='bg-[#333333] w-full h-[38vh] overflow-y-auto'>{/* <div className='bg-[#333333] w-full h-[15vh] overflow-y-auto'> */}
                  {
                    customers.map((customer, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 px-[1rem] leading-[1.8rem] border-b border-[#282828]'>
                          <p className='w-[80%]'>{customer.username ? customer.username : customer.businessname}</p>
                          <p className='w-[20%] text-right flex gap-6 justify-end'>
                            <button 
                                className={`text-md text-gray-100`}
                                onClick={() => handleDeleteCustomerModal(customer._id)}
                            ><FontAwesomeIcon icon={faTrash}/></button>
                            <button 
                                className={`text-md text-gray-100`}    
                                onClick={() => handleEditCustomerModal(customer._id)}        
                            ><FontAwesomeIcon icon={faEdit}/></button>
                          </p>
                        </div>
                      )
                    })
                  } 
                </div>
                <div className='absolute bottom-2 right-2'>
                  <div className='flex gap-6'>
                    <div className='cursor-pointer' onClick={()=>setContactModal(true)}>Contact/Support</div>
                    <div className='cursor-pointer'>FAQ</div>
                  </div>
                </div>
              </div>
            </div>
            <Sidebar />
        </div>
        <AddUserproxyModal isModal={userproxyModal} setIsModal={setUserproxyModal} seluser={seluser}/>
        <AddCustomerModal isModal={customerModal} setIsModal={setCustomerModal}/>
        <DeleteUserproxyModal isModal={userproxyDeleteModal} setIsModal={setUserproxyDeleteModal} userproxyID={userproxyID} selUserID={seluser._id} email={seluser.email} role={seluser.role}/>
        <DeleteUserModal isModal={customerDeleteModal} setIsModal={setCustomerDeleteModal} userID={customerID} selUserID={seluser._id} kind={'customer'}/>
        <EditUserproxyModal isModal={userproxyEditModal} setIsModal={setUserproxyEditModal} userproxyData={userproxyData}/>
        <EditCustomerModal isModal={customerEditModal} setIsModal={setCustomerEditModal} customerData={customerData}/>
        <ContactSupportModal isModal={contactModal} setIsModal={setContactModal}/>
    </Layout>
  );
}

export default Settings;